.parent-container {
  background-color: #07e056df; /* Black */
}

.container1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px; /* Add margin bottom for spacing */
}

.output {
  border: 6px solid #000000; /* White */
  padding: 20px;
  width: 70%;
  background-color: #0971e8c5; /* White */
  color: #e2c806; /* Black */
}

.welcome,
.user-info,
.user-type {
  padding: 10px;
  margin-bottom: 10px;
  color: #000000; /* Black */
}

.user-type {
  font-weight: bold;
}

.highlight-on-hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Add box-shadow for the 3D effect */
  transition: box-shadow 0.1s ease; /* Add transition effect */
  background-color: #FFFFFF; /* White */
  color: #cf0b0bcd; /* Black */
  border: 3px solid #000000;  /* Remove default button border */
  cursor: pointer; /* Add pointer cursor on hover */
  border-radius: 5px;
  padding: 12px 24px; /* Adjust padding */
  font-size: 16px; /* Adjust font size */
  transition: transform 0.5s ease-in-out;

}

.highlight-on-hover:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.6); /* Increase shadow on hover for more depth */
  transform: translateY(-10px);
}

.highlight-on-hover:active {
  background-color: #FFFFFF; /* White */
}

.student-details {
  margin-top: 20px;
  border: 6px solid #000000; /* White */
  padding: 20px;
  width: 70%;
  display: flex;
  flex-direction: row; /* Display cards side by side */
  flex-wrap: wrap; /* Allow cards to wrap to the next line if necessary */
  justify-content: center;
  background-color: #cd05dfc4; /* White */
  color: #000000; /* Black */
}

.student-card {
  border: 5px solid #100303de;
  padding: 10px; /* Reduced padding for a smaller size */
  margin: 10px; /* Add margin between cards */
  background-color: #cde307d7; /* White */
  width: calc(50% - 20px); /* Set width of each card, subtracting margin */
  max-width: 300px; /* Limit max width of the cards */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.5s ease-in-out; /* Smooth transition for box-shadow */
}

.student-card:hover {
  transform: translateY(-15px) translateX(15px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Highlighted box-shadow on hover */
}

.student-card h3 {
  margin-top: 5px; /* Add missing unit */
  font-size: 16px; /* Reduced font size of student name */
}

.student-card p {
  margin: 5px 0; /* Reduced paragraph margin */
}

.card-title {
  font-size: 18px;
  margin-bottom: 10px;
}

.cont2 {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}